<template>
  <div>
    <div v-show="this.gameOver" @click="this.restart()" style="font-size:15vmin; color:white; position:fixed; left:0; top:0; margin:0; padding:0; width:100%; height:100%; background-color:rgba(0,0,0,0.75); z-index:3; display:flex; flex-direction:column; align-items:center; justify-content:center;">
      <div>GAME OVER</div>
      <div>Try again?</div>
    </div>
    <div id="turns">
      <div class="landscape">TURNS </div>
      <div class="portrait">TURNS: </div>
      <div>{{ this.turns }}</div>
    </div>
    <div id="grid" style="position:fixed; margin:0; padding:0; left:50vw; top:50vh; transform:translate(-50%,-50%); width:100vmin; height:100vmin; background-color:black;">
      <div id="cell" v-for="(value, name) in this.cells" :key="name" :class="{ active:this.selected[0] === this.text2Vec(name)[0] && this.selected[1] === this.text2Vec(name)[1] }" :style="{ textAlign: 'center', color: 'white', position: 'absolute', fontSize: (100 / this.grid[0]) * 0.75 + 'vmin', padding: 0, margin: 0, width: 100 / this.grid[0] + '%', height: 100 / this.grid[0] + '%', left: value.position[0] * (100 / this.grid[0]) + '%', top: value.position[1] * (100 / this.grid[1]) + '%' }" @click="this.click(value.position)">
        {{ this.symbols[value.symbol] }}
      </div>
    </div>
    <div id="score">
      <div class="landscape">SCORE </div>
      <div class="portrait">SCORE: </div>
      <div>{{ this.score }}</div>
      <div v-show="this.score < 1000" class="portrait">&nbsp; NEXT:</div>
      <div v-show="this.score < 1000" class="landscape">NEXT</div>
      <div v-show="this.score < 1000">{{ (Math.floor(this.score / 100) + 1)*100 }}</div>
    </div>
  </div>
</template>

<script>
//import HelloWorld from './components/HelloWorld.vue'

export default {
  name: "App",
  components: {},
  data() {
    return {
      delta: 1000 / 60,
      grid: [8, 8],
      cells: {},
      symbols: ["ðŸ’¦", "ðŸŸ", "ðŸš", "ðŸ¦", "ðŸ¦€", "ðŸ¦‘"],//ðŸ£ðŸ§ŠðŸ’¦ðŸŸðŸšðŸ¦ðŸ¦€ðŸ¦‘ðŸª±
      clicked: [],
      selected: [],
      switched: [],
      clean: true,
      score: 0,
      turns: 0,
      gameOver: false,
      initialTurns: 15,
      sounds: []
    };
  },
  computed: {},
  methods: {
    text2Vec(text) {
      const parts = text.split("_");
      return [parseInt(parts[0]), parseInt(parts[1])];
    },
    vec2Text(vec) {
      return vec[0] + "_" + vec[1];
    },
    restart() {
      this.score = 0
      this.empty()
      this.populate()
      this.turns = this.initialTurns
      this.gameOver = false
    },
    empty() {
      Object.keys(this.cells).forEach((key) => delete this.cells[key]);
    },
    addScore(amount) {
      const lastScore = this.score
      this.score += amount
      if (lastScore < 1000 && this.score >= 1000) {
        this.turns += Infinity
      } else if (lastScore < 900 && this.score >= 900) {
        this.turns += this.initialTurns - 9
      } else if (lastScore < 800 && this.score >= 800) {
        this.turns += this.initialTurns - 8
      } else if (lastScore < 700 && this.score >= 700) {
        this.turns += this.initialTurns - 7
      } else if (lastScore < 600 && this.score >= 600) {
        this.turns += this.initialTurns - 6
      } else if (lastScore < 500 && this.score >= 500) {
        this.turns += this.initialTurns - 5
      } else if (lastScore < 400 && this.score >= 400) {
        this.turns += this.initialTurns - 4
      } else if (lastScore < 300 && this.score >= 300) {
        this.turns += this.initialTurns - 3
      } else if (lastScore < 200 && this.score >= 200) {
        this.turns += this.initialTurns - 2
      } else if (lastScore < 100 && this.score >= 100) {
        this.turns += this.initialTurns - 1
      }
    },
    check(position, symbol) {
      let matches = false;
      let index = 0;
      const horizontal = [];
      index = position[0] - 1;
      while (index + "_" + position[1] in this.cells) {
        if (this.cells[index + "_" + position[1]].symbol === symbol) {
          horizontal.push([index, position[1]]);
        } else {
          break;
        }
        index -= 1;
      }
      index = position[0] + 1;
      while (index + "_" + position[1] in this.cells) {
        if (this.cells[index + "_" + position[1]].symbol === symbol) {
          horizontal.push([index, position[1]]);
        } else {
          break;
        }
        index += 1;
      }
      const vertical = [];
      index = position[1] - 1;
      while (position[0] + "_" + index in this.cells) {
        if (this.cells[position[0] + "_" + index].symbol === symbol) {
          vertical.push([position[0], index]);
        } else {
          break;
        }
        index -= 1;
      }
      index = position[1] + 1;
      while (position[0] + "_" + index in this.cells) {
        if (this.cells[position[0] + "_" + index].symbol === symbol) {
          vertical.push([position[0], index]);
        } else {
          break;
        }
        index += 1;
      }
      if (horizontal.length >= 2 || vertical.length >= 2) {
        matches = true;
      }
      return matches;
    },
    populate() {
      for (let i = 0; i < this.grid[0]; i++) {
        for (let j = 0; j < this.grid[1]; j++) {
          if (!(i + "_" + j in this.cells)) {
            const symbols = [...this.symbols];
            while (symbols.length) {
              const symbol = parseInt(Math.random() * 10) % symbols.length;
              if (this.check([i, j], symbol)) { // number of symbols in this.symbols must be greater than 4
                symbols.splice(symbol, 1);
              } else {
                this.cells[i + "_" + j] = { symbol: symbol, position: [i, -1 * (this.grid[1] - j)] }
                break;
              }
            }
          }
        }
      }
    },
    shift() {
      let shifted = false;
      const speed = 0.01;
      Object.keys(this.cells).forEach((key) => {
        const dest = this.text2Vec(key)
        const diff = [dest[0] - this.cells[key].position[0], dest[1] - this.cells[key].position[1]]
        if (diff[0] !== 0 || diff[1] !== 0) {
          const rest = [Math.sign(diff[0]) * Math.min(Math.abs(diff[0]), speed * this.delta), Math.sign(diff[1]) * Math.min(Math.abs(diff[1]), speed * this.delta)]
          this.cells[key].position = [this.cells[key].position[0] + rest[0], this.cells[key].position[1] + rest[1]]
          if (!shifted) {
            shifted = true;
          }
        }
      });
      return shifted;
    },
    solve(position) {
      if (!(position[0] + "_" + position[1] in this.cells)) {
        return false;
      }
      let popped = false;
      const symbol = this.cells[position[0] + "_" + position[1]].symbol;
      let index = 0;
      const horizontal = [];
      index = position[0] - 1;
      while (index + "_" + position[1] in this.cells) {
        if (this.cells[index + "_" + position[1]].symbol === symbol) {
          horizontal.push([index, position[1]]);
        } else {
          break;
        }
        index -= 1;
      }
      index = position[0] + 1;
      while (index + "_" + position[1] in this.cells) {
        if (this.cells[index + "_" + position[1]].symbol === symbol) {
          horizontal.push([index, position[1]]);
        } else {
          break;
        }
        index += 1;
      }
      const vertical = [];
      index = position[1] - 1;
      while (position[0] + "_" + index in this.cells) {
        if (this.cells[position[0] + "_" + index].symbol === symbol) {
          vertical.push([position[0], index]);
        } else {
          break;
        }
        index -= 1;
      }
      index = position[1] + 1;
      while (position[0] + "_" + index in this.cells) {
        if (this.cells[position[0] + "_" + index].symbol === symbol) {
          vertical.push([position[0], index]);
        } else {
          break;
        }
        index += 1;
      }
      if (horizontal.length >= 2 || vertical.length >= 2) {
        const pops = {}
        Object.keys(this.symbols).forEach(keys => {
          pops[keys] = 0
        })
        if (horizontal.length >= 2) {
          horizontal.forEach((pos) => {
            const poppedSymbol = this.pop(pos)
            if (poppedSymbol !== undefined) {
              pops[poppedSymbol] += 1
            }
          });
        }
        if (vertical.length >= 2) {
          vertical.push([position[0], position[1]]);
          let lowest = undefined;
          let greatest = 0;
          vertical.forEach((pos) => {
            if (lowest === undefined || pos[1] > greatest) {
              lowest = pos;
              greatest = pos[1];
            }
          });
          for (let index = 0; index < vertical.length; index++) {
            const poppedSymbol = this.pop(lowest)
            if (poppedSymbol !== undefined) {
              pops[poppedSymbol] += 1;
            }
          }
        } else {
          const poppedSymbol = this.pop(position)
          if (poppedSymbol !== undefined) {
            pops[poppedSymbol] += 1;
          }
        }
        popped = true;
        if (this.sounds.length) {
          this.sounds[parseInt(Math.random() * 10) % this.sounds.length].play();
        }
        Object.keys(pops).forEach(key => {
          const value = pops[key]
          this.addScore(value * value)
        })
      }
      return popped;
    },
    pop(position) {
      let popped = undefined;
      if (!(position[0] + "_" + position[1] in this.cells)) {
        return popped;
      }
      let index = position[1];
      while (position[0] + "_" + index in this.cells) {
        if (position[0] + "_" + (index - 1) in this.cells) {
          this.cells[position[0] + "_" + index].symbol = this.cells[position[0] + "_" + (index - 1)].symbol;
          this.cells[position[0] + "_" + index].position = this.cells[position[0] + "_" + (index - 1)].position;
        } else {
          const symbol = this.cells[position[0] + "_" + index].symbol;
          delete this.cells[position[0] + "_" + index];
          popped = symbol
        }
        index -= 1;
      }
      return popped;
    },
    click(position) {
      this.clicked.length = 0;
      this.clicked.push(...position);
    },
    loop() {
      if (!this.shift()) {
        if (!this.clean) {
          this.clean = true;
          for (let i = 0; i < this.grid[0]; i++) {
            for (let j = 0; j < this.grid[1]; j++) {
              if (i + "_" + j in this.cells) {
                if (this.solve([i, j])) {
                  this.clean = false;
                }
              }
            }
          }
          if (!this.clean) {
            this.populate();
          }
        } else {
          if (this.turns <= 0) {
            this.gameOver = true
          } else if (this.switched.length) {
            const sOne = this.solve(this.switched);
            const sTwo = this.solve(this.selected);
            if (sOne || sTwo) {
              this.populate();
              this.clean = false;
            } else {
              const temp = JSON.parse(JSON.stringify(this.cells[this.switched[0] + "_" + this.switched[1]]));
              this.cells[this.switched[0] + "_" + this.switched[1]].symbol = this.cells[this.selected[0] + "_" + this.selected[1]].symbol;
              this.cells[this.switched[0] + "_" + this.switched[1]].position = this.cells[this.selected[0] + "_" + this.selected[1]].position;
              this.cells[this.selected[0] + "_" + this.selected[1]].symbol = temp.symbol;
              this.cells[this.selected[0] + "_" + this.selected[1]].position = temp.position;
            }
            this.switched.length = 0;
            this.selected.length = 0;
            this.turns -= 1
          } else if (this.clicked.length) {
            if (!this.selected.length) {
              this.selected.length = 0;
              this.selected.push(...this.clicked);
            } else if (this.clicked[0] !== this.selected[0] || this.clicked[1] !== this.selected[1]) {
              if ((Math.abs(this.clicked[0] - this.selected[0]) === 1 && Math.abs(this.clicked[1] - this.selected[1]) === 0) || (Math.abs(this.clicked[0] - this.selected[0]) === 0 && Math.abs(this.clicked[1] - this.selected[1]) === 1)) {
                this.switched.length = 0;
                this.switched.push(...this.clicked);
                const temp = JSON.parse(JSON.stringify(this.cells[this.switched[0] + "_" + this.switched[1]]));
                this.cells[this.switched[0] + "_" + this.switched[1]].symbol = this.cells[this.selected[0] + "_" + this.selected[1]].symbol;
                this.cells[this.switched[0] + "_" + this.switched[1]].position = this.cells[this.selected[0] + "_" + this.selected[1]].position;
                this.cells[this.selected[0] + "_" + this.selected[1]].symbol = temp.symbol;
                this.cells[this.selected[0] + "_" + this.selected[1]].position = temp.position;
              } else {
                this.selected.length = 0;
                this.selected.push(...this.clicked);
              }
            } else {
              this.selected.length = 0;
            }
            this.clicked.length = 0;
          }
        }
      }
    },
  },
  mounted() {
    for (let index = 0; index < 0; index++) {
      const newSound = new Audio('sound' + (index + 1) + '.wav');
      this.sounds.push(newSound)
    }
    this.turns = this.initialTurns
    this.populate();
    setInterval(() => { this.loop() }, this.delta);
  },
};
</script>

