    background-color: black;
  }
  #score {
    z-index: 1;
    display: flex;
    white-space: nowrap;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    height: calc((100vmax - 100vmin)*0.5);
    color: white;
    font-size: calc((100vmax - 100vmin)*0.125);
    width: 100vmin;
    text-align: center;
    background-color: black;
  }
  .portrait {
    display: block;
  }
  .landscape {
    display: none;
  }
}

@media screen and (orientation: landscape) {
  #turns {
    z-index: 1;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vmin;
    color: white;
    font-size: calc((100vmax - 100vmin)*0.125);
    width: calc((100vmax - 100vmin)*0.5);
    text-align: center;
    background-color: black;
  }
  #score {
    z-index: 1;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 0;
    top: 0;
    height: 100vmin;
    color: white;
    font-size: calc((100vmax - 100vmin)*0.125);
    width: calc((100vmax - 100vmin)*0.5);
    text-align: center;
